import React, { useState } from "react";
import { Redirect } from "react-router-dom";
import { reset_password } from "../redux/actions/auth";
import { useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";

function ResetPassword() {
	const { t } = useTranslation(["account"]);
	const [requestSent, sentRequestSent] = useState(false);
	const [email, setEmail] = useState("");
	const dispatch = useDispatch();

	const onSubmit = (e) => {
		e.preventDefault();

		dispatch(reset_password(email)).then((res) => {
			sentRequestSent(true);
		});
	};
	if (requestSent) {
		return <Redirect to="/" />;
	}

	return (
		<div
			className="bg-white pt-4 p-5 mx-4 mx-sm-auto"
			style={{
				borderRadius: "30px",
				maxWidth: "400px",
				margin: "70px auto",
				boxShadow: "0 10px 30px 0 rgba(68, 55, 77, 0.3)",
			}}
		>
			<h2 style={{ fontWeight: 700 }} className="text-center py-3 pb-4">
				{t("account:Request Password Reset")}
			</h2>
			<form onSubmit={(e) => onSubmit(e)}>
				<input
					type="email"
					className="form-control py-2"
					placeholder={t("account:Email")}
					name="username"
					value={email}
					style={{ borderRadius: "15px" }}
					onChange={(e) => setEmail(e.target.value)}
					required
				/>

				<div className="row">
					<button
						className="btn btn-primary mx-auto my-4 "
						style={{
							maxWidth: "300px",
							borderRadius: "15px",
							fontSize: "18px",
						}}
						type="submit"
					>
						{t("account:Reset Password")}
					</button>
				</div>
			</form>
		</div>
	);
}

export default ResetPassword;
